/* idenity color */
:root {
  --biggest-font-size: 24px;
  --h2-font-size: 20px;
  --h3-font-size: 18px;
  --normal-font-size: 15px;
  --small-font-size: 12px;
  --smaller-font-size: 14px;
  --mb-8: 8px;
  --mb-12: 12px;
  --mb-16: 16px;
  --mb-24: 24px;
  --mb-32: 32px;
  --mb-64: 64px;
  --mb-100: 100px;
  --red-color: #ff3150;
  --white-color: #fff;
  --yellow-color: #ffc400;
  --main-color: #19283f;
  --light-color: #a8b9c0;
  --lighter-color: #c5d0d5;
  --idenity-color-3: #06a2a2;
  --idenity-color-2: #00c6bf;
  --idenity-color-1: #33d1cc;
  --second-background-color: #eff7fa;
}
@media (min-width: 991px) {
  :root {
    --biggest-font-size: 36px;
    --h2-font-size: 28px;
    --h3-font-size: 20px;
    --normal-font-size: 18px;
    --small-font-size: 16px;
    --smaller-font-size: 14px;
  }
}

/** experimental grid mixins **/
/** experimental grid mixins **/
body {
  font-family: "Roboto", sans-serif;
  color: green;
}
body h1 {
  font-size: 60px;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #000;
}

.img-fluid {
  max-width: 100%;
}

a.button {
  display: block;
  background-color: var(--red-color);
  color: var(--yellow-color);
  padding: 8px 25px;
  cursor: pointer;
  border-radius: 15px;
  text-transform: capitalize;
  font-size: 15px;
  text-align: center;
  width: 75%;
  transition: 0.2s ease-in-out;
  margin: auto;
}
@media (min-width: 992px) {
  a.button {
    width: 15%;
  }
}
@media (min-width: 768px) and (max-width: 991.98px) {
  a.button {
    width: 30%;
  }
}
a.button:hover {
  transform: scale(1.01);
  color: var(--red-color);
  background-color: var(--yellow-color);
}

h2,
h3,
h4,
h5,
h6 {
  padding-bottom: 20px;
  text-transform: capitalize;
  font-weight: 400;
}

h1 {
  margin-bottom: 10px;
}

p {
  font-size: 15px;
  line-height: 1.4;
  color: var(--light-color);
}

.main-heading {
  text-align: center;
  margin: auto;
}
.main-heading i {
  color: var(--yellow-color);
  font-size: 42px;
  margin-bottom: 40px;
}
.main-heading h2 {
  color: var(--main-color);
  font-size: var(--biggest-font-size);
}
.main-heading p {
  color: var(--lighter-color);
  position: relative;
  margin-bottom: var(--mb-64);
  text-transform: uppercase;
}
.main-heading p:after {
  content: "";
  position: absolute;
  height: 1px;
  width: 200px;
  left: 50%;
  transform: translateX(-50%);
  bottom: -20px;
  background-color: var(--idenity-color-1);
}

/* start header */
header {
  position: relative;
  z-index: 2;
  height: 0;
}
header .container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-top: 15px;
}
@media (max-width: 991.98px) {
  header .container {
    justify-content: space-between;
  }
}
header .container .logo {
  width: 20%;
}
header .container nav {
  width: 70%;
  display: flex;
  justify-content: center;
}
@media (max-width: 991.98px) {
  header .container nav {
    display: none;
  }
  header .container nav.active {
    display: flex;
    flex-direction: column;
    background-color: var(--main-color);
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    position: absolute;
  }
}
header .container nav ul {
  display: flex;
  width: 50%;
  justify-content: space-evenly;
  border-right: solid 2px var(--idenity-color-3);
}
@media (max-width: 991.98px) {
  header .container nav ul {
    flex-direction: column;
    border-right: transparent;
    justify-content: center;
    text-align: center;
    width: 100%;
  }
}
header .container nav ul li a {
  display: block;
  color: var(--light-color);
  text-transform: capitalize;
  padding: 5px 1px;
  transition: 0.5s ease-in-out;
  font-size: 15px;
}
header .container nav ul li a.active, header .container nav ul li a:hover {
  color: var(--idenity-color-3);
}
@media (max-width: 991.98px) {
  header .container nav ul li a {
    padding-top: 25px;
    font-size: 20px;
  }
}
header .container .test {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 991.98px) {
  header .container .test {
    display: block;
    width: 100%;
    margin-top: var(--mb-64);
  }
}
header .container .test i {
  color: var(--idenity-color-3);
  cursor: pointer;
  margin-left: 32px;
}
@media (max-width: 991.98px) {
  header .container .test i {
    display: none;
  }
}
header .container .test a.button {
  width: 90px;
}
header .menu-toggle {
  color: red;
  font-size: 24px;
  color: var(--white-color);
  cursor: pointer;
  transition: 0.2s ease-in-out;
  z-index: 3;
  display: none;
}
header .menu-toggle i:nth-of-type(2) {
  display: none;
}
header .menu-toggle.active i:nth-of-type(1) {
  display: none;
}
header .menu-toggle.active i:nth-of-type(2) {
  display: block;
}
@media (max-width: 991.98px) {
  header .menu-toggle {
    display: block;
  }
}

/* start hero area */
.hero-area {
  position: relative;
  background-image: url("../../images/landing02.jpg");
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero-area .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(25, 40, 63, 0.9);
}

.hero-text {
  position: relative;
  text-transform: capitalize;
  text-align: center;
}
.hero-text h1,
.hero-text h2 {
  color: var(--white-color);
  font-weight: 300;
}
.hero-text h5 {
  color: var(--light-color);
}

/* end hero area */